import React, { Component, Fragment } from "react";
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import Head from "next/head";
import Page from "../layouts/main";
import Title from "../components/Title/Title";
import Footer from "../components/Footer/Footer";
import settings from "../util/settings";
import ReactGA from 'react-ga';
import data from '../static/json/data.json';

class Product extends Component {

  constructor(props) {
    super(props);
    this.state = {
      router: {},
      product: {}
    }
    this.ASSET_PATH = settings.ASSET_PATH + "images/"
  }

  componentDidMount() {
    var router = this.context.router,
        state = {};
    if (!settings.isServer()) {
      var urlChops = window.location.pathname.split("/"),
          lastChop = "", validURL = false
      for (var i = urlChops.length - 1; i > 0; i--) {
        if (urlChops[i] && urlChops[i] != "") {
          lastChop = urlChops[i]
          break
        }
      }
      for (var i = 0; i < data.products.length; i++) {
        if (data.products[i].url == lastChop) {
          state.product = data.products[i]
          validURL = true
        }
      }
      if (!validURL) router.push("/")
    }
    this.setState(state);
  }

  componentWillUnmount() {
  }

  render() {
    const { product } = this.state
    const { subcategories } = product || {}
    return (
      <Page id="Product" menu={{solidStyle: true}} ref={el => this.Page = el}>
        <div className="row pl-0" style={{ alignItems: 'flex-start' }}>
          <div className="col-md-12 prod-img-container" style={{ backgroundImage: `url(${this.ASSET_PATH + product.image})` }}>
            <h1 className="product-name">{ product.name }</h1>
          </div>
          <div className="col-md-10 offset-md-1 subproduct-container">
          { subcategories && subcategories.map((category, index) => {
              const colWidth = subcategories.length > 3 ? 4 : 12 / subcategories.length
              return (
                <div className={`category-container col-12 col-sm-${colWidth}`}>
                  <img className="category-image" src={`${this.ASSET_PATH}products/illustration.jpg`} />
                  <h2 className="category-name">{ category.name }</h2>
                  <div className="category-subproducts">
                    <ul>
                      { category.subproducts.map((item, index) => <li key={index}>{item}</li>) }
                    </ul>
                  </div>
                </div>
              )
            })
          }
          </div>
        </div>
        <Footer />
      </Page>
    )
  }
}

export default Product



